:root {
  --FONT-FAMILY: "Roboto", sans-serif;
  --RED: #ff001b;
  --WHITE: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--FONT-FAMILY);
  color: var(--WHITE);
  font-size: 12px;
  font-weight: 500;
}

.SectionHeader {
  background-image: url("mobile.0f89791d.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 230px;
}

.Nav {
  justify-content: space-between;
  align-items: center;
  padding: 40px 20px;
  display: flex;
}

.LogoHeader {
  display: flex;
}

.LogoImage {
  align-items: center;
  display: flex;
}

.LogoText {
  display: none;
}

.UlNav {
  gap: 40px;
  margin-block: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.LiNav {
  text-transform: uppercase;
  text-align: center;
  color: var(--WHITE);
  line-height: 14.06px;
  text-decoration: none;
  position: relative;
}

.LiNav:after {
  content: "";
  background-color: var(--RED);
  width: 0;
  height: 3px;
  transition: width .3s, left .3s;
  position: absolute;
  bottom: -4px;
  right: 0;
}

.LiNav:hover:after {
  width: 100%;
  left: 0;
}

.Current:after {
  content: "";
  background-color: var(--RED);
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.SearchForm {
  color: var(--WHITE);
  align-items: center;
  width: 280px;
  height: 20px;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.SearchInput {
  color: var(--WHITE);
  background-color: #0000;
  border: none;
  outline: none;
  flex-grow: 1;
  height: 100%;
  font-size: 16px;
}

.SearchButton {
  color: var(--WHITE);
  cursor: pointer;
  background-color: #0000;
  border: none;
}

.SearchIcon {
  fill: var(--WHITE);
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.SearchBorder {
  background-color: var(--WHITE);
  width: 100%;
  height: .5px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.SearchInput::placeholder {
  color: var(--WHITE);
  width: 96px;
  height: 16px;
}

.SearchInput:focus::placeholder {
  color: #0000;
}

@media screen and (width >= 425px) {
  .SectionHeader {
    background-image: url("tablet.0e69e7b4.jpg");
    height: 219px;
  }

  .Nav {
    padding: 40px 32px;
  }

  .LogoHeader {
    gap: 10px;
  }

  .LogoText {
    color: var(--WHITE);
    font-size: 30px;
    font-weight: 500;
    line-height: 35.16px;
    display: inline;
  }
}

@media screen and (width >= 769px) {
  .SectionHeader {
    background-image: url("desktop.b9220ae8.jpg");
